import React, { FunctionComponent } from 'react'
import Styles from './Header.module.styl'

import Image from 'gatsby-image'
import { Link } from '@reach/router'
import { useStaticQuery, graphql } from 'gatsby';
import { File } from '../../graphql-types'

type Props = {
  avatar: File
}

export const Header: FunctionComponent = props => {

  const data = useStaticQuery<Props>(graphql`
    query HeaderQuery {
      avatar: file(absolutePath: {regex: "/logo.png/"}) {
        childImageSharp {
          fixed(width: 45, height: 45) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <div className={Styles['container']}>
      <div className={Styles['header']}>
        <div className={Styles['logo']}>
          <Link className={Styles['home']} to='/'>
            <Image
              fixed={data.avatar.childImageSharp.fixed}
              alt={'icon'}
            />
          </Link>
        </div>
        <div className={Styles['navbar']}>
          <Link to='/' className={Styles['item']}>
            <i className='iconfont'>&#xe64f;</i>
            <span className={Styles['title']}>首页</span>
          </Link>
          {/* <Link to='/tags' className={Styles['item']}>
            <i className='iconfont'>&#xe63e;</i>
            <span className={Styles['title']}>标签</span>
          </Link>
          <Link to='/handbook' className={Styles['item']}>
            <i className='iconfont'>&#xe898;</i>
            <span className={Styles['title']}>手册</span>
          </Link> */}
          <a className={Styles['item']} href="https://github.com/MLuminary" target="_blank" rel="noopener noreferrer">
            <i className='iconfont'>&#xe716;</i>
            <span className={Styles['title']}>GitHub</span>
          </a>
          <Link to='/about' className={Styles['item']}>
            <i className='iconfont'>&#xe604;</i>
            <span className={Styles['title']}>关于</span>
          </Link>
        </div>
      </div>
    </div>
  )
}